@import "../../layouts/PublicLayout/Variable.scss";

.container--custom--navbar {
  display: none;
}

.not--found {
  height: 100%;
  margin-top: 100px;

  a {
    display: inline-block;
    padding: 0.75rem 1.25rem;
    border-radius: 8px;
    color: #fff;
    text-transform: uppercase;
    font-size: 1rem;
    letter-spacing: 0.15rem;
    transition: all 0.3s;
    position: relative;
    overflow: hidden;
    text-decoration: none;
    z-index: 1;
    &:after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #a3c6d9;
      z-index: -2;
    }
    &:before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 0%;
      height: 100%;
      background-color: darken(#647c84, 15%);
      transition: all 0.3s;
      z-index: -1;
    }
    &:hover {
      color: white;
      &:before {
        width: 100%;
      }
    }
  }

  a:hover {
    text-decoration: none !important;
    color: white;
    opacity: 80%;
  }
  .gif--not--found {
    display: block;
    margin: 0 auto;
    max-width: 500px;
    width: 100%;
  }

  .text--not--found {
    text-align: center;
    h1 {
      font-size: 50px;
      color: black;
      margin-bottom: 30px;
    }

    .title--not-found {
      color: #0a134f;
      margin-top: 20px;
      font-size: 40px;
      font-weight: 700;
      font-style: normal;
      letter-spacing: normal;
      line-height: 60px;
      margin: 20px auto;
      text-transform: uppercase;
    }
  }
}
